<template>
  <div>
    <h1>Hypobetical</h1>
    <b-container>
      <template v-for="rule in rules">
        <b-row>
          <b-col lg="5">
            <b-input-group prepend="Statistic">
              <b-form-select :options="statOptions" v-model="rule.statistic"/>
            </b-input-group>
          </b-col>
          <b-col lg="5">
            <b-input-group prepend="Weight (100%)">
              <b-form-input type="number" min="0" max="100" v-model="rule.weight"/>
            </b-input-group>
          </b-col>
          <b-col lg="2">
            <b-button variant="outline-danger" :disabled="rules.indexOf(rule)==0 && rules.length<=1" v-on:click="removeRule(rule)">x</b-button>
          </b-col>
        </b-row>
      </template>
      <br>
      <b-button v-on:click="addRule" :disabled="rules.length>=10">Add</b-button>
      <b-button variant="success" v-on:click="calculate" :disabled="!weightsAddTo100 || !statisticsSelected">Calculate</b-button>
    </b-container>
  </div>
</template>

<script>
export default {
  name: 'Main',
  data () {
    return {
      rules:[
        {
          statistic: null,
          weight: 0
        }
      ],
      statOptions: [
        "Passing Yards per Game",
        "Completion %",
        "Running Yards per Game",
        "Interceptions",
        "Fumbles",
        "Points per Game",
        "Opp Points per Game"
      ]
    }
  },
  methods: {
    addRule: function() {
      this.rules.push({statistic: null, weight: 0})
    },
    calculate: function(){
      alert("you can't beat the house")
    },
    removeRule: function(rule){
      this.rules.splice(this.rules.indexOf(rule),1)
    }
  },
  computed: {
    weightsAddTo100: function(){
      var total=0;
      for(var i=0; i< this.rules.length; i++){
        total+=parseInt(this.rules[i].weight);
      }
      return total===100;
    },
    statisticsSelected: function(){
      for(var i=0; i< this.rules.length; i++){
        if(!this.rules[i].statistic)
          return false;
      }
      return true;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



